	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2008-02-24-snelgrove-dies-away/index.md absPath of file >>> MarkdownRemark","html":"<p>This is sad news for Utahns. <a href=\"http://deseretnews.com/article/1,5143,695254897,00.html\">Snelgrove ice cream is going away</a>. When I was a kid I always loved caramel cashew. My grandfather used to buy it on their trips to Salt Lake City and carry it home in an ice chest with dry ice in it. They have been around for 79 years, but were bought out in 1990 by Dreyers. The sales volume of Snelgroves compared to Dreyers is too low for them to justify continuing the brand. This is truly a sad day.</p>","frontmatter":{"title":"Snelgrove Dies Away","date":"February 24, 2008","image":null}}},"pathContext":{"path":"/snelgrove-dies-away"}}

